import Typewriter from "typewriter-effect";
import './style.css';

function TypeWriter({ text }) {
    return (
        <div className="type-writer">
            <Typewriter
                onInit={(typewriter) => {
                    typewriter
                        .typeString(text)
                        .pauseFor(1000)
                        .deleteAll()
                        .typeString(text)
                        .start();
                }}
            />
        </div>
    );
}

export default TypeWriter;