import { PieChartComputers } from "../Charts/PieChartComputers"
import { PieChartSchool } from "../Charts/PieChartSchool"
import './style.css'

const Setup = () => {
    return (
        <div className="about-setup section-title">
            <h2>Namangan viloyati Mingbuloq tumani XTB ga qarasgli umumtalim maktablarining kompyuter jihozlari bilan taminlanishi.</h2>

            <hr />
            <div className="row">
                <div className="col-12 col-sm-4 px-5 px-sm-2">
                    <PieChartSchool />
                </div>
                <div className="col-12 col-sm-8 chart-text-wrapper">
                    <h3 className="chart-text">
                        Namangan viloyati Mingbuloq tumani XTB ga qarasgli 46 ta umumtalim maktablari,3 ta iqtisoslashgan maktab va 1ta davlat iqtisoslashgan maktab internati mavjud.
                    </h3>
                </div>
            </div>
            <hr />
            <div className="row">
                <div className="col-12 col-sm-4 px-5 px-sm-2">
                    <PieChartComputers />
                </div>
                <div className="col-12 col-sm-8 chart-text-wrapper">
                    <h3 className="chart-text">
                        Jami 62 ta informatika xonalari faol ishlab kelmoqda.Hozirgi kunda 62 ta informatika xonalarida 1195 ta kompyuter qurilmalari mavjud bolsada,shulardan 921 donasi soz holatda,274 donasi nosoz holatda ishlamoqda.1195 dona kompyuterlardan 688 donadi manan eskirgan kompyuterlar bolib,507 donasi zamonaviy kompyuterlardir.
                    </h3>
                </div>
            </div>
            <hr />
        </div>
    )
}

export default Setup